.header {
  max-width: 100%;
  border-bottom: 1px solid rgba(84, 84, 84, .7);
  margin: 28px 0 0 0;
  padding-bottom: 32px;
  display: flex;
  justify-content: space-between;
}

@media (min-width: 425px) {
  .header {
    max-width: 880px;
    margin: 28px auto 0 auto;
    padding-bottom: 41px;
  }
}

